<template>
    <div>
        <p id="countName">{{ name }}</p>
        <div class="iCountUp">
            
            
            <i-count-up
                :start="0"
                :end="count"
                :decimals="0"
                :duration="2.5"
                :options="options"
                
            ></i-count-up>
        </div>
    </div>
</template>

<script>
import ICountUp from 'vue-countup-v2';
export default {
    props: ['name', 'countTo', 'id'],
    data(){
        return {
            options: {
                useEasing: true,
                useGrouping: true,
                separator: ',',
                decimal: '.',
                prefix: '',
                suffix: '',
                
            },
            count: parseInt(this.countTo)
        }
    },
    methods: {
       callback: function(ins) {
        ins.update(ins.endVal + 100);
      }
    },
    components: {
        ICountUp
    }
}
</script>

<style>
    .iCountUp {
    font-size: 2em;
    margin: 0;
    font-weight: 700;
    color: white;
  }
    #countName{
        color: white;
        text-transform: uppercase;
        font-size: .9em;
    }
</style>


