<template>
    <section class="section plans-section">
        <section class="section section-header">
            <div class="columns">
                <div class="column">
                    <h2>Insurance plans</h2><br>
                     <span>Here you can see our insurance plans that include individual,<br>
                   group and family plans.</span>
                </div>
            </div>
        </section>
        <div class="columns">
            <div class="column cover" >
            
            </div>
             <div class="column">
                <div class="select-plan-type-div">
                    <div :class="{ active: selectedColumn == 1, selectTab: true}" @click="selectedColumn = 1" :style="{}">
                        <i class="fa fa-user-o fa-2x"></i>
                        Personal Plan
                    </div>
                     <div :class="{ active: selectedColumn == 2, selectTab: true}" @click="selectedColumn = 2">
                         <i class="fa fa-home fa-2x"></i>
                        Family Plan
                    </div>
                     <div :class="{ active: selectedColumn == 3, selectTab: true}" @click=" selectedColumn = 3">
                         <i class="fa fa-users fa-2x"></i>
                        Group Plan
                    </div>
                </div>
                <!-- Box 1-->
                <div id="first-box" v-if="selectedColumn == 1 ">
                    <h3> What does our personal plan include?</h3>
                        <ul>
                           <li>
                                <i class="fa fa-check-circle-o" aria-hidden="true"></i>
                                Prescription drugs from your doctor.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Emergency medical service.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Help with injury recovery
                            </li>
                        </ul>
                    
                    <div class="columns">
                        <div class="column">
                            <button class="button is-primary"> read more</button>
                        </div>
                        <div class="column">
                            <button class="button is-success"> get a quote</button>
                        </div>
                    </div>
                </div>
                <!-- End Box 1-->
                <!-- Box 2-->
                <div id="first-box" v-if="selectedColumn == 2 ">
                    <h3> What does our personal plan include?</h3>
                        <ul>
                            
                            <li>
                                <i class="fa fa-check-circle-o fa-lg" aria-hidden="true"></i>
                                Prescription drugs from your doctor.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Emergency medical service.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Help with injury recovery
                            </li>
                        </ul>
                    
                    <div class="columns">
                        <div class="column">
                            <button class="button is-primary"> read more</button>
                        </div>
                        <div class="column">
                            <button class="button is-success"> get a quote</button>
                        </div>
                    </div>
                </div>
                <!-- End Box 2-->
                <!-- Box 3 -->
                <div id="first-box" v-if="selectedColumn == 3 ">
                    <h3> What does our personal plan include?</h3>
                        <ul>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Prescription drugs from your doctor.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Emergency medical service.
                            </li>
                            <li>
                                <i class="fa fa-check-circle-o"></i>
                                Help with injury recovery
                            </li>
                        </ul>
                    
                    <div class="columns">
                        <div class="column">
                            <button class="button is-primary"> read more</button>
                        </div>
                        <div class="column">
                            <button class="button is-success"> get a quote</button>
                        </div>
                    </div>
                </div>
                <!-- End Box 3-->
            </div>
        </div>
    </section>
</template>

<script>

export default {

        data() {
            return {
                selectedColumn: '1',
                counter: 1221,
                count: 0,
                seconds: 0
            }
        },
        methods: {

        }
    }
</script>

<style scoped>
     .fa-check-circle-o{
        float: left;
        color: rgb(162, 226, 255);
    }
    h2{
        font-size: 2.5em;
        font-weight:300;
        letter-spacing: 2px;
        
    }
    h3{
        margin-bottom: 50px;
    }
    span{
        color: rgba(100,100,100,0.6);
    }
    .section-header{
        padding-bottom: 100px;
    }
    .select-plan-type-div{
        border-bottom: 1px solid rgba(200,200,200,0.5);
        display: flex;
        justify-content: space-around;

    }
    .cover{
        background: url('../assets/old-lady.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        height: 300px;
        margin-right: 60px;
        margin-top: 1%;
    }
    @media (max-width: 845px){
        .cover{
            margin-right: 0;
            margin-bottom: 50px;
        }
        .fa-check-circle-o{
            float: none;
            display: inline;
            margin-right: 15px;
        }
        
    }
    .select-plan-type-div div{
        padding: 15px;
       
    }
    .select-plan-type-div div:hover{
         cursor: pointer;
    }
    .active{
        border-bottom: 2px solid rgb(31, 181, 250);
    }
    .selectTab{
        text-transform:uppercase;
        font-size: 0.8em;
        padding: 20px;
        animation: clicked 1s ease-in forwards;
    }
    #first-box{
        margin-top: 50px;
        margin-bottom: 50px;
    }
    #first-box ul li{
        display: block;
        border-bottom: 1px solid rgba(200,200,200,0.2);
        padding-bottom: 15px;
        font-size: 0.85em;

        color: rgba(120,120,120,0.7);
    }
    i{
        display: block;
        margin-bottom: 10px;
    }
    .is-primary{
        background: rgb(241, 240, 240);
        background: -webkit-linear-gradient(to right, #f0f0f0, #dbdbdb); 
        background: linear-gradient(to right, #f0f0f0, #ececec); 
        color: rgb(144, 207, 238);
        font-weight: 700;
        
    }
    .is-primary:hover{
        background: rgb(241, 240, 240);
        background: -webkit-linear-gradient(to left, #f0f0f0, #dbdbdb); 
        background: linear-gradient(to left, #f0f0f0, #ececec); 
        color: rgb(144, 207, 238);
    }
    .is-success{
        background: #15B4FF;
        background: -webkit-linear-gradient(to right, #5691c8, #457fca);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to right, #527da8, #457fca); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        color: white;
        font-weight: 700;
    }

    .is-success:hover{
        background: #15B4FF;
        background: -webkit-linear-gradient(to right, #5691c8, #457fca);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to left, #527da8, #457fca); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    }
   
</style>


