<template>
  <div id="app">
    <navbar></navbar>
    <sub-header id="sub-header"></sub-header>
    <hero></hero>
    <call-us id="call-us"></call-us>
    <insurance-plans id="insurance-plans"></insurance-plans>
    <our-experience></our-experience>
    <our-experience-extended></our-experience-extended>
    <google-maps-embed></google-maps-embed>
    <insurance-footer></insurance-footer>
   
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import Hero from './components/Hero.vue';
import CallUs from './components/CallUs.vue';
import SubHeader from './components/SubHeader.vue';
import GoogleMapsEmbed from './components/GoogleMapsEmbed.vue';
import InsurancePlans from './components/InsurancePlans.vue';
import OurExperience from './components/OurExperience.vue';
import OurExperienceExtended from './components/OurExperienceExtended.vue';
import InsuranceFooter from './components/InsuranceFooter.vue';

export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  components: {
    Navbar,
    Hero,
    CallUs,
    SubHeader,
    InsurancePlans,
    OurExperience,
    OurExperienceExtended,
    InsuranceFooter,
    GoogleMapsEmbed
  }
}
</script>

<style lang="scss">
$primary-color: #15B4FF;
#app {
  font-family: 'Raleway', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;

}
#call-us, #insurance-plans, #sub-header{
  width: 80%;
  margin: auto;
}
@media(m-width: 865px ){
  #call-us, #insurance-plans, #sub-header{
  width: 100%;
  margin: auto;
}
}
#call-us{
  margin-top: 60px;
}


h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

</style>



