<template>
    <section class="section" id="experience-section">
        <div class="container">
            <div class="columns">
                <div class="column">
                    <counter name="projects" countTo= "500" ></counter>

                </div>
                <div class="column">
                    <counter name="contracts" countTo="1200"></counter>
                </div>
                <div class="column">
                    <counter name="clients" countTo="700"></counter>
                </div>
                <div class="column">
                    <counter name="hours" countTo="900"></counter>
                </div>
            </div>
        </div>
        <div class="video-responsive">
              <iframe src="http://www.youtube.com/embed/i9wGIaKE6fA"
   width="560" height="315" frameborder="0" allowfullscreen></iframe>
        </div>  
        
        
    </section>
    
    
</template>

<script>
    import Counter from './Counter.vue';
    export default {
        data(){
            return{

            }
        },
        components: {
            Counter
        }
    }

</script>

<style >
    #experience-section{
        background: #001641;
        height: 500px;
        margin-bottom: 300px;
    }
    .video-responsive{
        overflow:hidden;
        padding-bottom:56.25%;
        position:relative;
        height:0;
       
    }
    .video-responsive iframe{
        left:50%;
        top:50%;
        height:55%;
        transform:translate(-50%, -50%);
        width:55%;
        position:absolute;
        -webkit-box-shadow: 6px 23px 28px -16px rgba(0,0,0,0.35);
-moz-box-shadow: 6px 23px 28px -16px rgba(0,0,0,0.35);
box-shadow: 6px 23px 28px -16px rgba(0,0,0,0.35);

    }
    @media (max-width: 1100px){
        .video-responsive iframe{
            height: 70%;
            width: 70%;
        }
    }
    @media(max-width: 1000px){
        .video-responsive iframe{
            height: 80%;
            width: 80%;
            top: 60%;
        }
    }
    @media (max-width: 900px){
        .video-responsive iframe{
            height: 100%;
            width: 100%;
            top: 70%;
            
        }
    }
    
</style>


